import { TASK_KEY, useTaskDetail } from "./../../utils/task";
import { KANBAN_KEY } from "./../../utils/kanban";
import { useProjectDetail } from "./../../utils/project";
import { useLocation } from "react-router";
import { useUrlQueryParam } from "utils/url";
import { useCallback, useMemo } from "react";

export const useProjecIdInUrl = () => {
  const { pathname } = useLocation();
  const id = pathname.match(/project\/(\d+)/)?.[1];
  return Number(id);
};

export const useProjectInUrl = () => useProjectDetail(useProjecIdInUrl());

export const useKanbanSearcnParams = () => ({
  projectId: useProjecIdInUrl(),
});
export const useKanbansQueryKey = () => [KANBAN_KEY, useKanbanSearcnParams()];

export const useTaskSearcnParams = () => {
  const [param] = useUrlQueryParam(["name", "typeId", "processorId", "tagId"]);
  const projectId = useProjecIdInUrl();
  return useMemo(
    () => ({
      projectId,
      typeId: Number(param.typeId) || undefined,
      processorId: Number(param.processorId) || undefined,
      tagId: Number(param.tagId) || undefined,
      name: param.name,
    }),
    [projectId, param]
  );
};
export const useTasksQueryKey = () => [TASK_KEY, useTaskSearcnParams()];

export const useTaskModal = () => {
  const [{ editingTaskId }, setEditingTaskId] = useUrlQueryParam([
    "editingTaskId",
  ]);

  const { data: task, isLoading } = useTaskDetail(Number(editingTaskId));

  const startEdit = useCallback(
    (id: number) => {
      setEditingTaskId({ editingTaskId: id });
    },
    [setEditingTaskId]
  );

  const close = useCallback(() => {
    setEditingTaskId({ editingTaskId: "" });
  }, [setEditingTaskId]);

  return {
    editingTaskId,
    task,
    startEdit,
    close,
    isLoading,
  };
};
