import { QueryKey, useMutation, useQuery } from "react-query";
import { Epic } from "./../types/index";
import { useHttp } from "./http";
import { useAddConfig, useDeleteConfig } from "./use-optimistic-options";
export const Epic_KEY = "=EpicS";

export const useEpics = (params?: Partial<Epic>) => {
  const client = useHttp();
  // 第二个参数，param改变 触发重新请求
  return useQuery<Epic[]>([Epic_KEY, params], () =>
    client("epics", { data: params })
  );
};

export const useAddEpic = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    (params: Partial<Epic>) =>
      client(`epics`, {
        data: params,
        method: "POST",
      }),
    useAddConfig(queryKey)
  );
};

export const useDelEpic = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    ({ id }: { id: number }) => client(`epics/${id}`, { method: "DELETE" }),
    useDeleteConfig(queryKey)
  );
};
